import React from 'react'
import Tittle from '../Components/Tittle'
import ImageSection from '../Components/ImageSection';
import SkillsSection from '../Components/SkillsSection';
import ServicesSection from '../Components/ServicesSection';
import design from '../img/design.svg';
import intelligence from '../img/intelligence.svg';

function AboutPage() {
    return (
        <div className="AboutPage">
            <Tittle title={'About Me'} span={'About Me'} />
            <ImageSection />
            <Tittle title={'My Skills'} span={'My Skills'} />
            <div className="skillsContainer">
                <SkillsSection skill={'C/C++'} progress={'60%'} width={'60%'} />
                <SkillsSection skill={'JavaScript/Typescript'} progress={'30%'} width={'30%'} />
                <SkillsSection skill={'React Js'} progress={'70%'} width={'70%'} />
                <SkillsSection skill={'Node Js'} progress={'80%'} width={'80%'} />
                <SkillsSection skill={'Python'} progress={'90%'} width={'90%'} />
                <SkillsSection skill={'Java'} progress={'80%'} width={'80%'} />
                <SkillsSection skill={'Web Design'} progress={'25%'} width={'25%'} />
                <SkillsSection skill={'Data Structure & Algorithm'} progress={'76%'} width={'76%'} />
            </div>

            <Tittle title={'Areas of Interests'} span={'Areas of Interests'} />
            <div className="servives-container">
                <ServicesSection image={design} title={'Web Development'} 
                text={'Creating beautiful websites that help people and make a difference'}
                />
                <ServicesSection image={intelligence} title={'Android App Development'} 
                text={'I love developing apps that can impact millions in a good way'}
                />
                <ServicesSection image={intelligence} title={'Machine Learning'} 
                text={'Well-structured individual with skills in developing amazing statistical model solutions'}
                />
            </div>

        </div>
    )
}

export default AboutPage;
